import styles from "./Drawer.module.scss"
import {Link} from "react-router-dom"
import Loader from "react-loader-spinner";

function Drawer({currentWeather, currentDate, currentTime}) {

  return (
    <div className={styles.drawer} >
      <div className={styles.drawerHeader}>
        <Link to={'/'}><i class="fas fa-cloud-sun-rain fa-2x"></i><h2>React Weather</h2></Link>
      </div>
      <div className={styles.drawerBody}>
        <ul>
          <li> <Link to={'/'}><i class="fas fa-stream"></i>–ì–ª–∞–≤–Ω–∞—è –ø–∞–Ω–µ–ª—å</Link> </li>
          <li> <Link to={'/forecast'}><i class="fas fa-satellite-dish"></i>–ü—Ä–æ–≥–Ω–æ–∑</Link></li>
          <li> <Link to={'/statistics'}><i class="fas fa-atlas"></i>–°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ —Ä–µ–≥–∏–æ–Ω–∞</Link></li>
          <li> <Link to={'/settings'}><i class="fas fa-cog"></i>–ù–∞—Å—Ç—Ä–æ–π–∫–∏</Link> </li>
        </ul>
      </div>
      {/* –ö–∞—Ä—Ç–æ—á–∫–∞ —Å —Ç–µ–∫—É—â–µ–π –ø–æ–≥–æ–¥–æ–π –¥–ª—è –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ –≥–æ—Ä–æ–¥–∞ */}
      <div className={styles.drawerCurrentCard}>
        <div className={styles.wrapper}>
           <div className={styles.cardHeader}>
              {currentWeather.weather && <img src={`img/openWeather/${currentWeather.weather[0].icon}@2x.png`} alt="Icon" />}
              <div className={styles.cardHeaderText}>
                <p>–°–µ–≥–æ–¥–Ω—è</p>
                <p>{currentTime}</p>
                <p>{currentDate}</p>
              </div>
            </div>
        <div className={styles.container}>
          {/* –¢–µ–∫—É—â–∞—è —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞ */}
          <p className={styles.currentTemp}>{currentWeather.main.temp && `${(currentWeather.main.temp).toFixed(1)}\u00B0C`}</p>
          {/* –í—ã–±—Ä–∞–Ω–Ω—ã–π –≥–æ—Ä–æ–¥ */}
          <p className={styles.currentCity}>{currentWeather.name}</p>
          {/* –ö–æ–¥ —Å—Ç—Ä–∞–Ω—ã */}
          <p className={styles.currentState}>{currentWeather.sys.country}</p>

          {/* =========== –í–õ–ê–ñ–ù–û–°–¢–¨ ============ */}
          <div className={styles.currentHumidity}><p>–í–ª–∞–∂–Ω–æ—Å—Ç—å</p><p>{currentWeather.main.humidity && `${(currentWeather.main.humidity).toFixed(1)}%`}</p></div>
          <div className={styles.progressHumidity}><div style={{width: `${currentWeather.main.humidity}%`}}></div></div>

          {/* ========== –û–ë–õ–ê–ß–ù–û–°–¢–¨ ============ */}
          <div className={styles.currentVisibility}>
            <p>–û–±–ª–∞—á–Ω–æ—Å—Ç—å</p>
            <p>{`${currentWeather.clouds.all}%`}</p>
          </div>
          <div className={styles.progressVisibility}><div style={{width: `${currentWeather.clouds.all}%`}}></div></div>

          {/* ========== –í–ï–¢–ï–† ============ */}
          <div className={styles.currentWind}>
            <p>–í–µ—Ç–µ—Ä</p>
            <i class="fas fa-wind"></i>
            <p>{currentWeather.wind.speed && `${(currentWeather.wind.speed).toFixed(1)} –º/—Å`}</p>
          </div>

           <Loader
              type="ThreeDots"
              color="#6a58f3"
              height={40}
              width={40}
            />
          
        </div>
      </div>
    </div>

    </div>
  )
}

export default Drawer
