import Header from "../../components/Header"
import {useState, useEffect} from "react"
import axios from "axios";
import styles from "./Forecast.module.scss"
import {
  Chart as ChartJS,
  LinearScale,
  CategoryScale,
  BarElement,
  PointElement,
  LineElement,
  Legend,
  Tooltip,
  LineController,
  BarController
} from 'chart.js';
import { Chart } from 'react-chartjs-2';
ChartJS.register(
  LineController,
  LinearScale,
  CategoryScale,
  BarElement,
  PointElement,
  LineElement,
  Legend,
  Tooltip,
  BarController
);

function Forecast() {

  const API_KEY = "8459744fe826f74f3d531b5bfb2929de";
  const [city, setCity] = useState('{"lat": 51.507351, "lon":-0.127758}');
  const [labels, setLabels] = useState([]);
  const [tempLine, setTempLine] = useState([]);
  const [feelsLike, setFeelsLike] = useState([]);
  const [dewPoint, setDewPoint] = useState([]);

  // –∑–∞–ø—Ä–∞—à–∏–≤–∞–µ–º –ø—Ä–æ–≥–Ω–æ–∑ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ –≥–æ—Ä–æ–¥–∞
  useEffect(()=> {
    axios.get(`https://api.openweathermap.org/data/2.5/onecall?lat=${JSON.parse(city).lat}&lon=${JSON.parse(city).lon}&exclude=current,minutely,hourly,alerts&appid=${API_KEY}&units=metric`)
      .then(res => {
        console.log(res.data.daily);
        getDateLabels(res.data.daily);
        setTempLine(computeTempLine(res.data.daily));
        setFeelsLike(computeFeelsLikeTemp(res.data.daily));
        setDewPoint(computeDewPoint(res.data.daily));
      }).catch(err => alert(err));
  }, [city]);

  // —Å–ª—É—à–∞–µ–º —Å–µ–ª–µ–∫—Ç
  const handleOnChange = (e)=> {
    setCity(e.target.value);
    console.log(city);
  }

   // –ø–æ–ª—É—á–∞–µ–º –ª–µ–π–±–ª—ã - –¥–∞—Ç—ã
  const getDateLabels = (arr) => {
    const labs = arr.map(item => (new Date(item.dt * 1000).toLocaleDateString("ru-RU")));
    setLabels(labs);
    console.log(labels);
  }

  // —Ä–∞—Å—á–µ—Ç –¥–ª—è –≥—Ä–∞—Ñ–∏–∫–∞ —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä—ã
  const computeTempLine = (res) => {
      return res.map((item) => item.temp.day);
  }

  // —Ä–∞—Å—á–µ—Ç –¥–ª—è –≥—Ä–∞—Ñ–∏–∫–∞ —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä—ã (–æ—â—É—â–∞–µ—Ç—Å—è –∫–∞–∫)
  const computeFeelsLikeTemp = (res) => {
      return res.map((item) => item.feels_like.day);
  }

  // —Ä–∞—Å—á–µ—Ç –¥–ª—è –≥—Ä–∞—Ñ–∏–∫–∞ —Ç–µ–º–ø–µ—Ä–∞—Ç—É—Ä—ã (–æ—â—É—â–∞–µ—Ç—Å—è –∫–∞–∫)
  const computeDewPoint = (res) => {
      return res.map((item) => item.dew_point);
  }



  // –∫–æ–Ω—Ñ–∏–≥ –≥—Ä–∞—Ñ–∏–∫–∞
  const data = {
  labels,
  datasets: [
    {
      type: 'line',
      label: `–¢–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞ C`,
      borderColor: '#6a58f3',
      borderWidth: 6,
      fill: true,
      data: tempLine,
    },
    {
      type: 'bar',
      label: '–û—â—É—â–∞–µ—Ç—Å—è –∫–∞–∫ C',
      backgroundColor: 'rgb(75, 192, 192)',
      data: feelsLike,
      borderColor: 'white',
      borderWidth: 2,
    },
    {
      type: 'bar',
      label: '–¢–æ—á–∫–∞ —Ä–æ—Å—ã',
      backgroundColor: 'rgb(53, 162, 235)',
      data: dewPoint,
    },
  ],
}

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Chart.js Line Chart',
      },
    },
  };

  return (
    <div className={styles.container}>
      <Header />
      <div className={styles.pageContainer}>
        <h2 className={styles.pageName}>–ü—Ä–æ–≥–Ω–æ–∑ –≤ 
          <select className={styles.selectCity} onChange={handleOnChange}>
            <option value='{"lat": 51.507351, "lon":-0.127758}'>–õ–æ–Ω–¥–æ–Ω–µ</option>
            <option value='{"lat": 55.755825, "lon":37.617298}'>–ú–æ—Å–∫–≤–µ</option>
            <option value='{"lat": 40.712776, "lon":-74.005974}'>–ù—å—é-–ô–æ—Ä–∫–µ</option>
            <option value='{"lat": 48.856613, "lon": 2.352222}'>–ü–∞—Ä–∏–∂–µ</option>
            <option value='{"lat": 59.934280, "lon":30.335098}'>–°–∞–Ω–∫—Ç-–ü–µ—Ç–µ—Ä–±—É—Ä–≥–µ</option>
          </select>
        </h2>

         <div className={styles.chartContainer}>
           <Chart type='bar' options={options} data={data} />
         </div>

      </div>
    </div>
  )
}

export default Forecast
